import React from "react"

import * as PropTypes from "prop-types"
import Img from "gatsby-image"
import Helmet from 'react-helmet'
import { rhythm } from "../utils/typography"

import Bio from './../components/bio/Bio'
const propTypes = {
  data: PropTypes.object.isRequired,
}

class ProjectTemplate extends React.Component {

  render() {
    const project = this.props.data.contentfulProject
    const {
      projectName,
      projectCopy,
      heroImage,
      category
    } = project

    let _renderCats;

    if(category){
      _renderCats=  category.map(_ => <li key={_.title.title}><img width="30" src={_.icon.file.url} alt={_.title.title} /></li>)
    }

    return (
      <div style={{
        backgroundColor: '#efefef',
        boxShadow: '0px 0px 0px 5px rgba(255,255,255,.2)'
      }}>
      
        <Helmet title={`${projectName} 👨🏼‍💻 Matt Bagni`} />
        <div style={{ transform: `scale(1.05)` }} >
          <Img sizes={heroImage.sizes} />
        </div>
        <div style={{ padding: `1rem` }}>

          <h1 style={{
            position: `relative`,
            marginTop: `-2rem`,
            fontSize: '8vw',
            color: '#111',
            textShadow: `4px 4px #fff`
          }}>{projectName}</h1>

          <ul style={{
            display: `flex`,
            listStyleType: `none`,
            margin: 0
          }}>
            {_renderCats}
          </ul>

          <div
            dangerouslySetInnerHTML={{
              __html: projectCopy.childMarkdownRemark.html,
            }}
          />
        </div>
        <hr style={{ width: `30%`, margin: `1rem auto`, background: `#fff` }} />
        <Bio bio={false} social={true} />
      </div>
    )
  }
}

ProjectTemplate.propTypes = propTypes

export default ProjectTemplate

export const pageQuery = graphql`
  query projectQuery($id: String!) {
    contentfulProject(id: { eq: $id }) {
      projectName
      projectLink
      projectCopy {
        childMarkdownRemark {
          html
        }
      }
      heroImage {
        sizes {
          base64
          aspectRatio
          src
          srcSet
          sizes
        }
      }
      category {
        icon {
          file{
            url
          }
        }
        title {
          title
        }
      }
    }
  }
`



